@import "./variables.scss";

html {
	min-height: 100%;
}

.nice-scrolls {
	&::-webkit-scrollbar {
		width: 11px;
	}
	&::-webkit-scrollbar-track {
		background-color: $backgroundUp;
		border-radius: 4rem;
	}
	&::-webkit-scrollbar-thumb {
		background-color: $textDown;
		border-radius: 4rem;
	}
}

body {
	font-family: "SourceSansPro", -apple-system, BlinkMacSystemFont, "Segoe UI",
		Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	text-rendering: optimizeLegibility;
	font-weight: 400;
	margin: 0;
	font-size: 1rem;
	background: linear-gradient($backgroundUp, $backgroundDown);
	min-height: 100%;
}

.no-focus-outline {
	*:focus {
		outline: none;
	}
}

.container {
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 1.5rem;
}

.innerContainer {
	max-width: 900px;
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	width: 100%;
}

.title {
	color: $textDown;
	padding-top: 2rem;
	font-size: 2rem;
	font-weight: 700;
	font-family: "PlayfairDisplay";

	@include desktop {
		font-size: 3rem;
		font-weight: 400;
	}
}

.form {
	display: flex;
	flex-direction: column;
	width: 100%;
	max-width: 400px;
	font-family: inherit;

	&__label {
		font-family: inherit;
		display: flex;
		flex-direction: column;
		font-size: 1.5rem;
		font-weight: 500;
		color: $textUp;
		padding-bottom: 1.5rem;
		width: 100%;
	}

	&__input {
		font-family: inherit;
		margin-top: 0.75rem;
		padding: 0.75rem;
		border-radius: 0.75rem;
		font-size: 1rem;
		border: none;
		outline: none;
		box-sizing: content-box;
		font-weight: 400;
		transition: all 0.2s cubic-bezier(0.165, 0.84, 0.44, 1);
		color: $textDown;

		&:focus {
			box-shadow: 0px 0px 0.5px 2px $primary;
		}
	}

	&__submit {
		padding: 0.75rem;
		font-size: 1.25rem;
		font-weight: 400;
		border: none;
		color: $up;
		border-radius: 0.75rem;
		background: $primary;
		cursor: pointer;
		font-family: inherit;
		width: 100%;
	}
}

.paragraph {
	font-size: 1.5rem;
	font-weight: 300;
	color: $textUp;
	padding-bottom: 1.5rem;
	margin: 0;
	max-width: 55ch;
}
